              label=""
              width="150"
              sortable>
            </el-table-column>
            <el-table-column
              prop="updateTime"
              label=""
              width="150"
              sortable>
            </el-table-column>
            <el-table-column label="">
              <el-table-column
                prop="readNum"
                label=""
                width="120">
              </el-table-column>
              <el-table-column
                prop="commentNum"
                label=""
                width="120"
                sortable>
