                              <el-button size="small" type="primary" @click="handleEdit(scope.row)">
                                  <a class="el-icon-edit" style="color: white;"></a> 
                              </el-button>
                              <el-button size="small" type="danger" >
                                  <a class="el-icon-circle-close" style="color: white;"></a> 
                              </el-button>
                              <el-button v-if="scope.row.topStatus === 0 && scope.row.publishStatus === 1" size="small" type="warning">
                                  <a class="el-icon-upload2" style="color: white;"></a> 
                              </el-button>
                              <el-button v-if="scope.row.topStatus === 1" size="small" type="warning">
                                  <a class="el-icon-setting" style="color: white;"></a> 
                              </el-button>

