            </el-form-item>
            <el-form-item>
                <el-button   type="primary" size="small" @click="resetForm('searchForm')" icon="el-icon-refresh"></el-button>
            </el-form-item>
        </el-form>
        <el-table
          :data="tableData3"
          style="width: 100%"
          height="480">
          <el-table-column
              prop="cover"
              label=""
              fixed
              width="120">
            </el-table-column>
            <el-table-column
              prop="title"
              label=""
              fixed
              width="120">
              <template slot-scope="scope">
                  <a @click="handleDetail()">{{scope.row.title}}  <i class="el-icon-view"></i></a>
              </template>
            </el-table-column>
            <el-table-column
              prop="createTime"
              label=""
              width="120"
              sortable>
            </el-table-column>
            <el-table-column
              prop="delTime"
              label=""
              width="150"
              sortable>
            </el-table-column>
            <el-table-column
              prop="tags"
              label=""
              width="120">
            </el-table-column>
            <el-table-column
              prop="comment"
              label=""
              width="120">
            </el-table-column>
            <el-table-column
              prop="remarks"
              label=""
              width="120">
            </el-table-column>
            <el-table-column
              prop="publishTime"
              label=""
              width="150"
              sortable>
            </el-table-column>
            <el-table-column
              prop="updateTime"
              label=""
              width="150"
              sortable>
            </el-table-column>
            <el-table-column label="">
