                sortable>
              </el-table-column>
              <el-table-column
                prop="pointNum"
                label=""
                width="300"
                sortable>
              </el-table-column>
            </el-table-column>
            <el-table-column
                label=""
                width="200"
                fixed="right">
                <template slot-scope="scope">
                          <el-button-group >
                              <el-button size="small" type="info">
                                  <a class="el-icon-refresh" style="color: white;"></a> 
                              </el-button>
                              <el-button size="small" type="danger">
                                  <a class="el-icon-circle-close" style="color: white;"></a> 
                              </el-button>
                          </el-button-group>
                      </template>
