<script>
  export default {
    data() {
      return {
        addFormVisible:false,
        detailFormVisible:false,
        editFormVisible:false,
        tableData3: [{
          cover:'',
          title:'1',
          createTime:'2017-12-6',
          updateTime:'2017-12-6',
          publishTime:'2017-12-6',
          topStatus:1,
          publishStatus:1,
          tags:[1,2,3],
          content:'1',
          remarks:'1',
          readNum:25,
          commentNum:2,
          pointNum:1,
        }, {
          cover:'',
          title:'2',
          createTime:'2017-12-6',
          updateTime:'2017-12-6',
          publishTime:'2017-12-6',
          topStatus:0,
          publishStatus:0,
          tags:[1,2,3],
          content:'2',
          remarks:'2',
          readNum:25,
          commentNum:2,
          pointNum:1,
